/* stylelint-disable */
@font-face {
  font-family: "TTFirsNeue-Regular";
  src: url("../fonts/../fonts/TTFirsNeue-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "TTFirsNeue-DemiBold";
  src: url("../fonts/../fonts/TTFirsNeue-DemiBold.woff2") format("woff2");
  font-weight: 600;
  font-display: swap;
  font-style: normal;
}
* {
  padding: 0;
  margin: 0;
  border: 0;
}

*::-webkit-scrollbar {
  width: 0;
  height: 0;
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html,
body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input,
button,
textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: inherit;
}

img,
picture {
  width: auto;
  height: auto;
  max-width: 100%;
}

.wrapper {
  display: flex;
  padding: 40px 0 60px 0;
  overflow: hidden;
}
.wrapper__container {
  width: 100%;
  display: flex;
}

.main {
  flex: 1 1 auto;
  width: 100%;
}

@media screen and (max-width: 1024px) {
  .wrapper {
    padding: 20px 0 30px 0;
  }
  .wrapper__container {
    flex-direction: column;
    padding: 0 !important;
  }
}
[class*=__container] {
  max-width: 1350px;
  margin: 0 auto;
  padding: 0 15px;
}

body {
  font-family: "TTFirsNeue-Regular", sans-serif;
  font-style: normal;
  font-size: 14px;
  line-height: 22px;
  letter-spacing: 0.02em;
  color: #1F344C;
  background: #E7EAEE;
}
body._lock {
  overflow: hidden;
}

.main {
  margin: 0 20px 0 0;
}

.aside {
  min-width: 315px;
  max-width: 315px;
}
@media screen and (max-width: 600px) {
  .aside {
    min-width: 300px;
    max-width: 100%;
  }
}

.popup-subscribe-email {
  position: fixed;
  z-index: 120;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgba(17, 17, 17, 0.8);
  backdrop-filter: blur(50px);
  transition: opacity 0.7s ease, visibility 0.7s ease;
}
.popup-subscribe-email._hidden {
  opacity: 0;
  visibility: hidden;
}
.popup-subscribe-email__wrapper {
  max-width: 630px;
  padding: 0 15px;
}
.popup-subscribe-email__title {
  font-family: "TTFirsNeue-DemiBold", sans-serif;
  font-size: 30px;
  line-height: 30px;
  text-transform: uppercase;
  color: #FFFFFF;
  text-align: center;
  margin: 0 0 25px 0;
}
.popup-subscribe-email__title span:first-child {
  color: #0450FF;
}
.popup-subscribe-email__title span:nth-child(2) {
  font-style: italic;
}
.popup-subscribe-email__info {
  background: #111111 url("../img/bg-popup-email-desc.jpg") no-repeat 50% 0/cover;
  position: relative;
  padding: 93px 30px 99px;
  border-radius: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media screen and (max-width: 620px) and (orientation: portrait) {
  .popup-subscribe-email__info {
    background: #111111 url("../img/bg-popup-email-mob.jpg") no-repeat 50% 0/cover;
  }
}
.popup-subscribe-email__info-image {
  max-width: 114px;
  margin: 0 0 15px 0;
}
.popup-subscribe-email__info-title {
  font-family: "TTFirsNeue-DemiBold", sans-serif;
  max-width: 515px;
  color: #FFFFFF;
  font-size: 32px;
  line-height: 40px;
  letter-spacing: 0.01em;
  text-align: center;
  margin: 0 0 30px 0;
}
.popup-subscribe-email__info-title span {
  display: inline-block;
  position: relative;
  top: 4.5px;
  font-family: "TTFirsNeue-Regular", sans-serif;
  font-style: italic;
  padding: 6px 12px 6px;
  font-size: 28px;
  line-height: 29px;
  text-transform: uppercase;
  letter-spacing: 0.02em;
  background: #0450FF;
  border-radius: 56.535px;
}
.popup-subscribe-email__info-wrap {
  width: 100%;
  background: #292929;
  box-shadow: 0px 5px 20px rgba(0, 0, 0, 0.2);
  border-radius: 14px;
  padding: 15px;
  display: flex;
  min-width: 535px;
  margin: 0 0 10px 0;
}
.popup-subscribe-email__info-input {
  border: 1px solid rgba(162, 155, 171, 0.22);
  color: #FFFFFF;
  flex: 1 1 auto;
  display: inline-block;
  margin: 0 10px 0 0;
  background: rgba(255, 255, 255, 0.12);
  box-shadow: inset 0px 0px 14px rgba(0, 0, 0, 0.6);
  border-radius: 8px;
  padding: 14px 24px;
}
.popup-subscribe-email__info-input::placeholder {
  font-family: "TTFirsNeue-Regular", sans-serif;
  font-size: 14px;
  line-height: 20px;
  letter-spacing: 0.01em;
  color: #8E8E98;
}
.popup-subscribe-email__info-sub {
  font-family: "TTFirsNeue-DemiBold", sans-serif;
  font-size: 13px;
  line-height: 20px;
  text-align: center;
  letter-spacing: 0.04em;
  color: #FFFFFF;
  padding: 14px 26px;
  background: #0450FF;
  border-radius: 8px;
  transition: background-color 0.3s ease;
}
.popup-subscribe-email__info-sub:hover {
  background: #13399b;
}
.popup-subscribe-email__info-text {
  align-self: flex-end;
  font-family: "TTFirsNeue-Regular", sans-serif;
  font-size: 11px;
  line-height: 20px;
  color: #A29BAB;
  text-align: end;
}
.popup-subscribe-email__info-text a {
  white-space: nowrap;
  color: #FFFFFF;
  text-decoration: underline;
}
.popup-subscribe-email__info-text a:hover {
  text-decoration: none;
}
.popup-subscribe-email__info-btn-close {
  cursor: pointer;
  position: absolute;
  top: 15px;
  right: 15px;
  width: 24px;
  height: 24px;
  background: url("../img/btn-close-white.svg") no-repeat 0 0/contain;
  transition: transform 0.3s ease;
}
.popup-subscribe-email__info-btn-close:hover {
  transform: scale(1.06);
}

@media screen and (max-width: 620px) and (orientation: portrait) {
  .popup-subscribe-email {
    align-items: flex-end;
  }
  .popup-subscribe-email__wrapper {
    max-width: 100%;
    min-width: 100%;
    padding: 0;
  }
  .popup-subscribe-email__info {
    padding: 110px 15px 35px;
    border-radius: 20px 20px 0 0;
  }
  .popup-subscribe-email__info-title {
    font-size: 24px;
    line-height: 30px;
    max-width: 328px;
    margin: 0 0 27px 0;
  }
  .popup-subscribe-email__info-title span {
    top: -1.5px;
    font-size: 19px;
    line-height: 19px;
  }
  .popup-subscribe-email__info-wrap {
    flex-direction: column;
    align-items: stretch;
    padding: 0;
    background: transparent;
    min-width: 100%;
    margin: 0 0 15px 0;
  }
  .popup-subscribe-email__info-input {
    background: rgba(255, 255, 255, 0.16);
    box-shadow: inset 0px 0px 14px rgba(0, 0, 0, 0.4);
    margin: 0 0 10px 0;
    padding: 20px 20px 18px;
  }
  .popup-subscribe-email__info-input::placeholder {
    font-size: 16px;
    line-height: 20px;
    color: #FFFFFF;
  }
  .popup-subscribe-email__info-sub {
    padding: 14px 15px 13px;
    width: 100%;
    font-size: 15px;
    line-height: 20px;
  }
  .popup-subscribe-email__info-text {
    align-self: flex-start;
    text-align: start;
    font-size: 13px;
    line-height: 18px;
  }
}
@media screen and (max-width: 920px) and (max-height: 450px) and (orientation: landscape) {
  .popup-subscribe-email__wrapper {
    transform: scale(0.8);
  }
}
@media screen and (max-width: 920px) and (max-height: 412px) and (orientation: landscape) {
  .popup-subscribe-email__wrapper {
    transform: scale(0.7);
  }
}
@media screen and (max-width: 920px) and (max-height: 375px) and (orientation: landscape) {
  .popup-subscribe-email__wrapper {
    transform: scale(0.6);
  }
}
@media screen and (max-width: 920px) and (max-height: 320px) and (orientation: landscape) {
  .popup-subscribe-email__wrapper {
    transform: scale(0.5);
  }
}